@import 'src/style/variable.scss';

.imgloading {
  &__loading {
    display: none;
    opacity: 0;
    visibility: hidden;
  }

  &.load {
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      @extend %img;
    }

    .imgloading__loading {
      display: block;
      opacity: 1;
      visibility: visible;
      z-index: 1;
      overflow: hidden;
      @extend %load;
    }
  }
}
